.hr {
    animation-duration: 1250ms;
    animation-iteration-count: 1;
    height: 170px;
    transform-origin: bottom;
}

.jump {
    animation-name: jump;
    animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1);
}

.Tooltip {
     overflow-x: auto;
 }

@keyframes jump {
    0%   { transform: translateY(0); }
    10%  { transform: translateY(0); }
    30%  { transform: translateY(-180px); }
    50%  { transform: translateY(0px); }
    57%  { transform: translateY(-7px); }
    64%  { transform: translateY(0); }
    100% { transform: translateY(0); }
}

@font-face {
    font-family: Font;
    src: url(../font.otf);
}

.score_text {
    font-family: Font;
    font-size: 56px;
    font-weight: 800;
    letter-spacing: 8px;
    vertical-align: middle;
}

.scores {
    font-family: Font;
    font-size: 22px;
    font-weight: 400;
    display: flex;
    align-items: center;
    justify-content: center;
}
.score_right {
    margin-left: auto;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}