.card-button {
    padding-bottom: 8px;
    padding-top: 8px;
    border-radius: 10px;
    -webkit-box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.01);
    box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.01);
    margin: 8px 16px;
}

.card-button .inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-flex-pack: start;
    justify-content: start;
}

.card-button .img {
    width: 70px;
    min-width: 70px;
    height: 70px;
    margin: 8px;
}

.card-button .other {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
}

.card-button .title {
    height: 22px;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.25;
    letter-spacing: -0.4px;
    color: #fff;
    margin-bottom: 4px;
    margin-right: 20px;
}

.card-button .desc {
    opacity: 0.72;
    font-size: 12px;
    line-height: 1.23;
    letter-spacing: -0.1px;
    color: #fff;
    margin-right: 20px;
}